.mainScreen {
    /*position: fixed;*/
    height: 100vh;
    width: 100vw;
    background-color: var(--background-primary);
}

.rightSide,
.leftSide {
    float: left;
    width: 50%;
}

.centerSide {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.centerImage {
    overflow: hidden;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.centerImage img {
    /*width: 427.06px;*/
    height: 325px;
    transition: 0.6s cubic-bezier(0.43, 0.13, 0.23, 0.96);
}

.centerImage img:hover {
    transform: scale(1.1);
    filter: blur(4px);
}

.centerText {
    position: absolute;
    pointer-events: none;
    white-space: nowrap;
    display: block;
    width: min-content;
    overflow: hidden;
}

.centerText span {
    display: inline-block;
    transform: translateY(150%);
    transition: 0.6s;
    font-family: var(--font-secondary);
    font-style: normal;
    font-weight: 550;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: -0.03em;
}


.centerImage,
.centerText {
    z-index: 10;
}

.centerImage img:hover + .centerText span {
    opacity: 1;
    transform: translateY(0);
}

.centerBackgroundSymbols {
    position: absolute;
    z-index: -99;
    width: min-content;
    font-family: var(--font-title);
    font-style: normal;
    font-weight: 500;
    font-size: 127px;
    line-height: 100%;
    color: var(--text-fourth);
    opacity: 0;
    transition: 0.6s;
}

.centerImage img:hover + p + p {
    opacity: 1;
}

.leftSide {
    padding-left: 30px;
    padding-top: 41px;
}

.userName {
    font-family: var(--font-title);
    width: min-content;
    font-style: normal;
    font-weight: 500;
    font-size: 175px;
    line-height: 100%;
    letter-spacing: -0.03em;
}

.aboutMe0 {
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    width: 275px;
    color: var(--text-secondary);
    margin-top: 36px;
}

.designCredits {
    margin-top: 98px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: max-content;
    height: max-content;
    color: var(--text-primary)
}

.designCredits p:nth-child(2) {
    color: var(--text-third)
}

.scroller {
    margin-top: 85px;
    border: 2px solid var(--scroller-color);
    width: 74px;
    height: 74px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scroller p {
    width: min-content;
    color: var(--text-third);
}

.aboutMe1 {
    position: absolute;
    bottom: 30px;
    right: 30px;
    width: min-content;
    text-align: right;
    font-family: var(--font-title);
    font-style: normal;
    font-weight: 500;
    font-size: 146px;
    line-height: 100%;
}

.otherText0 {
    position: absolute;
    top: 282px;
    right: 84px;
    font-family: var(--font-primary);
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    color: var(--text-third);
    width: 266px;
}

.menu {
    position: absolute;
    top: 38px;
    right: 33px;
    text-align: right;
}

.menuContent {
    margin-top: 38px;
}

.menuContent p {
    margin-top: 5px;
}

.menuContent p:nth-child(1) {
    color: var(--text-third)
}