@font-face {
    src: url("./sources/fonts/SFUIDisplay.ttf");
    font-family: 'SF UI Display';
}

@font-face {
    src: url("./sources/fonts/NeueMontreal.otf");
    font-family: 'Neue Montreal';
}

@font-face {
    src: url("./sources/fonts/HelveticaNeueCyr.otf");
    font-family: 'Helvetica Neue';
}

*, *:after, *:before {
    padding: 0;
    margin: 0;
    outline: none;
    border: none;
    box-sizing: border-box;
}

:root {
    --background-primary: #060606;
    --text-primary: #fff;
    --text-secondary: #B5B5B5;
    --text-third: #FF3737;
    --text-fourth: rgba(255, 55, 55, 0.6);
    --font-primary: 'SF UI Display';
    --font-secondary: 'Helvetica Neue';
    --font-title: 'Neue Montreal';
    --scroller-color: #303030;
}

body {
    background-color: var(--background-primary);
    color: var(--text-primary);
    font-family: var(--font-primary);
    overflow-x: hidden;
}